<template>
  <div>
    <div class="hljs" v-html="compiledMarkdown()" v-highlight></div>
  </div>
</template>

<script>
import Marked from 'marked'

export default {
  name: 'Help',
  comments: {
    Marked
  },
  computed: {
  },
  methods: {
    compiledMarkdown () {
      let markDown = `
### 快捷键说明
| # | | 快捷键 | | 说明 |
| --------: | | :-- | :--------| :-- |
| 1 | | Ctrl + C | | 复制单元格，注意：进入复制模式时，将无法编辑单元格格式，包括字体、背景色等格式   |
| 2 | | Ctrl + V | | 粘贴单元格  |
| 3 | | Ctrl + Z | | 撤销 |
| 4 | | Ctrl + Y | | 重做 |
| 5 | | Ctrl + S | | 保存表单 |
| 6 | | F1      | | 打开帮助文档 |
| 7 | | F2      | | 编辑单元格 |
| 8 | | ESC      | | 取消复制单元格 |
| 9 | | DELETE      | | 清空单元格内容 |
      `

      return Marked(markDown, { sanitize: true })
    }
  }
}
</script>

<style scoped>

</style>



